import { Component, OnInit } from '@angular/core';
import { PostService } from '../services/post.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-wishlist',
  templateUrl: './wishlist.component.html',
  styleUrls: ['./wishlist.component.css']
})
export class WishlistComponent implements OnInit {

  constructor(private postService: PostService,private router: Router) { }

  post = {
    name : null,
    description: null,
    imgsrc: null,
    price: 0,
    category: null,
    postId: null,
    postedBy: null,
  }

  postIds = []
  posts = []
  noPosts = false

  ngOnInit() {
    if(sessionStorage.getItem('username')==null){
      this.router.navigate(["/login"])
      return
    }
    this.postService.getWishlistPosts(sessionStorage.getItem('username')).subscribe(data => {
      this.postIds = data
      if(this.postIds.length==0){
        this.noPosts = true
      }else{
        for(let i=0;i<this.postIds.length;i++){
          this.postService.getPostData(this.postIds[i]).subscribe(data => {
            this.post = data
            if(this.post!=null){
              this.posts.push(this.post)
            }
          },error => this.router.navigate(["/error"]))
        }
      }
    },error => this.router.navigate(["/error"]))
  }

  removeFromWishlist(postId){
    if(sessionStorage.getItem('username')==null){
      this.router.navigate(["/login"])
    }
    this.postService.removeFromWishlist(postId,sessionStorage.getItem('username')).subscribe(data => {
      // console.log(data)
      this.posts = this.posts.slice(0,0)
      this.postService.getWishlistPosts(sessionStorage.getItem('username')).subscribe(data => {
        this.postIds = data
        if(this.postIds.length==0){
          this.noPosts = true
        }else{
          for(let i=0;i<this.postIds.length;i++){
            this.postService.getPostData(this.postIds[i]).subscribe(data => {
              this.post = data
              if(this.post!=null){
                this.posts.push(this.post)
              }
            },error => this.router.navigate(["/error"]))
          }
        }
      },error => this.router.navigate(["/error"]))
    },error => {this.router.navigate(["/error"])})
  }

  openPost(postId){
    this.router.navigate(['/post'],{queryParams: {postId: postId}})
  }

  

}
