import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http'
import { Observable } from 'rxjs';
import { Post } from '../models/Post';
import { ImgurImage } from '../models/ImgurImage';

@Injectable({
  providedIn: 'root'
})
export class PostService {
  
  private _getPostUrl = 'http://localhost:8081/post'
  private _allPostsUrl = 'http://localhost:8081/posts'
  private _createPostUrl = "http://localhost:8081/post/create"
  private _checkPostUrl = "http://localhost:8081/post/dataValid"
  private _postUpdateUrl = "http://localhost:8081/post/update"
  private _deletePost = "http://localhost:8081/post/delete"
  private _imgurUploadUrl = "https://api.imgur.com/3/image"
  private _searchPostsUrl = 'http://localhost:8081/posts/search'
  private _searchByCatAndKeyUrl = 'http://localhost:8081/posts/search'
  private _searchByCategoryUrl = 'http://localhost:8081/posts/search'
  private _getPostsByUserNameUrl = 'http://localhost:8081/posts/postedBy'
  private _getWishlistUrl = 'http://localhost:8081/wishlist'
  private _removeFromWishlistUrl = 'http://localhost:8081/remove-from-wishlist'
  private _addToWishlistUrl = 'http://localhost:8081/add-to-wishlist'
  private _inMyWishlistUrl = 'http://localhost:8081/in-my-wishlist'
  
  constructor( private http : HttpClient) { }
  
  inMyWishlist(postId: any, userName: string):Observable<boolean> {
    return this.http.get<boolean>(this._inMyWishlistUrl+'/'+userName+'/'+postId)
  }

  addToWishlist(postId: any, userName: string):Observable<String> {
    return this.http.get<String>(this._addToWishlistUrl+'/'+userName+'/'+postId)
  }

  removeFromWishlist(postId: String,userName:String):Observable<String> {
    return this.http.get<String>(this._removeFromWishlistUrl+"/"+userName+"/"+postId)
  }

  getWishlistPosts(userName: string):Observable<Post[]> {
    return this.http.get<Post[]>(this._getWishlistUrl+'/'+userName)
  }

  deletePost(postId: String):Observable<String> {
    return this.http.get<String>(this._deletePost+"/"+postId)
  }

  getPostsByUserName(userName: string):Observable<Post[]> {
    return this.http.get<Post[]>(this._getPostsByUserNameUrl+'/'+userName)
  }

  getPostsByCategory(categorySearch: String) {
    return this.http.get<any>(this._searchByCategoryUrl+'/category/'+categorySearch)
  }

  getSearchedPosts(searchKeyword: String):any {
    return this.http.get<any>(this._searchPostsUrl+'/keyword/'+searchKeyword)
  }
  
  getPostsByKeywordAndCategory(searchKeyword: String, categorySearch: String):any {
    return this.http.get<any>(this._searchByCatAndKeyUrl+'/'+categorySearch+'/'+searchKeyword)
  }

  getPostData(postId: String):Observable<Post> {
    return this.http.post<Post>(this._getPostUrl,postId)
  }

  fetchPosts():Observable<Post[]>{
    return this.http.get<Post[]>(this._allPostsUrl)
  }
  
  checkPostValid(post) {
    return this.http.post<any>(this._checkPostUrl, post)
  }

  updatePost(post) {
    return this.http.post<any>(this._postUpdateUrl, post)
  }
  
  getImageUrl(file: File) : Observable<ImgurImage> {
    const headers = new HttpHeaders({ Authorization : "Client-ID 8f4c5d652e63019"});
    return this.http.post<ImgurImage>(this._imgurUploadUrl,file,{headers})
  }

  createPost(post) {
    return this.http.post<any>(this._createPostUrl,post)
  }

}
