import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http'
import { Observable } from 'rxjs';
import { Router } from '@angular/router';
import { User } from '../models/User';
import { setServers } from 'dns';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  
  
  private _registerUrl = "http://localhost:8081/user/create"
  private _checkDataUrl = "http://localhost:8081/user/dataValid"
  private _checkUpateDataUrl = "http://localhost:8081/user/updateDataValid"
  private _loginUrl = "http://localhost:8081/user/checkLogin"
  private _userDetails = "http://localhost:8081/user/username"
  private _updateUserDetails = "http://localhost:8081/user/update"
  private _reports = 'http://localhost:8081/user/reports'
  private _users = 'http://localhost:8081/users'
  private _deleteUser = 'http://localhost:8081/user/delete/'
  private _reportSubmit = 'http://localhost:8081/user/reportUser'
  
  
  loggedInUserUsername
  
  constructor(private http: HttpClient,private router : Router) { }
  
  checkUpdateDataValid(fetchedUserDetails: { firstName: any; lastName: any; userName: any; email: any; password: any; mobileNumber: any; locationState: any; locationCountry: any; userImgUrl: any; }) {
    return this.http.post<any>(this._checkUpateDataUrl,fetchedUserDetails)
  }

  updateUserDetails(fetchedUserDetails: { firstName: any; lastName: any; userName: any; email: any; password: any; mobileNumber: any; locationState: any; locationCountry: any; userImgUrl: any; }) {
    return this.http.post<any>(this._updateUserDetails,fetchedUserDetails)
  }

  checkDataValid(user) {
    return this.http.post<any>(this._checkDataUrl, user)
  }
  
  getUserDetails(userName: string):Observable<User> {
    return this.http.get<User>(this._userDetails+"/"+userName)
  }
  
  registerUser(user){
    return this.http.post<any>(this._registerUrl, user)
  }
  

  checkLogin(credentials: {}){
    return this.http.post<any>(this._loginUrl, credentials)
  }

  getLoggedInUser(){
    let storedToken = atob(sessionStorage.getItem('user'))
    let index = storedToken.indexOf(':');
    let email = storedToken.slice(0,index)
    let password = storedToken.slice(index+1,storedToken.length)
    console.log(email,password)
    // let returnedUser = this.checkLogin({'email' : email,'password' : password})
    // console.log(returnedUser)
  }
  loggedIn(){
    let storedString = sessionStorage.getItem('user')
    if(storedString==null){
      return false
    }else{
      let storedToken = atob(storedString)
      let index = storedToken.indexOf(':')
      let email = storedToken.substring(0,index)
      let password = storedToken.substring(index+1)
      let returnedUser = this.checkLogin({email:email,password:password})
      if(returnedUser==null){
        return false
      }else{
        return true
      }
    }
  }

  logout(){
    sessionStorage.removeItem('user')
    sessionStorage.removeItem('username')
    this.loggedInUserUsername = ""
    sessionStorage.clear()
    this.router.navigate(['/home'])
  }

  adminLoggedIn(){
    let token  = sessionStorage.getItem('admin')
    if(token==null){
      return false
    }else{
      if(token==='admin'){
        return true
      }else{
        return false
      }
    }
  }

  fetchReports() {
     return this.http.get<any>(this._reports)
  }

  fetchUsers(){
    return this.http.get<any>(this._users)
  }

  deleteUser(userName){
   return this.http.get(this._deleteUser+'/'+userName)
  }

  reportUser(report){
    return this.http.post(this._reportSubmit,report)
  }
  
}
