import { Component, OnInit } from '@angular/core';
import { AuthService } from '../services/auth.service';
import { Router } from '@angular/router'

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  registerUserData = {
    firstName : "",
    lastName : "",
    email : "",
    password : "",
    mobileNumber : "",
    locationState : "",
    locationCountry : "India",
    userName : ""
  }
  message: any
  userNameValid = true;
  emailValid = true;
  mobileNumberValid = true;
  userCheck = {userNameValid:true,emailValid:true,mobileNumberValid:true}

  constructor(private authService: AuthService, private router: Router) { }

  ngOnInit() {
  }

  reenteredPassword : String = ''

  checkDataValid(){
    //client side validation 
    if(this.firstNameNotValid()||this.lastNameNotValid()||this.userNameNotValid()||this.emailNotValid()||
      this.passwordNotValid()||this.reenteredPasswordNotValid()||this.numberNotValid()){
        return
      }
    //check if all three unique data is valid 
    else{
    this.authService.checkDataValid(this.registerUserData).subscribe(data =>{ 
      this.userCheck = data;
      this.userNameValid = this.userCheck.userNameValid;
      this.emailValid = this.userCheck.emailValid;
      this.mobileNumberValid = this.userCheck.mobileNumberValid;
      //if all data is valid, lets register the user real quick
      if(this.userNameValid&&this.emailValid&&this.mobileNumberValid){
        this.authService.registerUser(this.registerUserData).subscribe(data => 
          {
            // console.log("user registered");
            sessionStorage.setItem('user', btoa(data.email + ":" + data.password))
            sessionStorage.setItem('username', data.userName)
            this.router.navigate(['/posts'])
          },error => this.router.navigate(["/error"]))
      }
    },error => this.router.navigate(["/error"]))
    }
  }

  firstNameNotValid(){
    if(this.registerUserData.firstName.length>0&&this.registerUserData.firstName.length<2){
      return true
    }else{
      return false
    }
  }
  lastNameNotValid(){
    if(this.registerUserData.lastName.length>0&&this.registerUserData.lastName.length<2){
      return true
    }else{
      return false
    }
  }
  userNameNotValid(){
    if(this.registerUserData.userName.length>0&&this.registerUserData.userName.length<6){
      return true
    }else{
      return false
    }
  }
  emailNotValid(){
    if(this.registerUserData.email.length==0){
      return false
    }
    if(this.registerUserData.email.includes('@')&&this.registerUserData.email.charAt(this.registerUserData.email.length-1)!=='@'){
      return false;
    }else{
      return true
    }
  }
  passwordNotValid(){
    if(this.registerUserData.password.length==0){
      return false
    }
    if(this.registerUserData.password.length>=8&&(this.registerUserData.password.includes('@')||
      this.registerUserData.password.includes('.')||this.registerUserData.password.includes('^')||
      this.registerUserData.password.includes('*')||this.registerUserData.password.includes('$')||
      this.registerUserData.password.includes('#')||this.registerUserData.password.includes('!')||
      this.registerUserData.password.includes('&')||this.registerUserData.password.includes('_')||
      this.registerUserData.password.includes('-'))){
        return false
      }else{
        return true
      }
  }
  reenteredPasswordNotValid(){
    if(this.reenteredPassword!==this.registerUserData.password){
      return true
    }else{
      return false
    }
  }
  numberNotValid(){
    if((this.registerUserData.mobileNumber+"").length!=10&&(this.registerUserData.mobileNumber+"").length>0){
      return true
    }else{
      return false
    }
  }
}
