import { Component, OnInit } from '@angular/core';
import { RouterModule, Router, ActivatedRoute } from '@angular/router';
import { Post } from '../models/Post';
import { PostService } from '../services/post.service';
import { AuthService } from '../services/auth.service';

@Component({
  selector: 'app-post',
  templateUrl: './post.component.html',
  styleUrls: ['./post.component.css']
})
export class PostComponent implements OnInit {

  public userDetails = {
    userName : null,
    firstName : null,
    lastName : null,
    email : null,
    mobileNumber : null,
    locationState : null,
    locationCountry : null,
    userImgUrl : null,
    password : null
  }

  postId: String
  public postData : Post = new Post()

  constructor(private router: Router, private route : ActivatedRoute,private postService: PostService,private authService:AuthService) { }

  ngOnInit() {
    this.route.queryParams.subscribe(params => {
      this.postId = params['postId']
      this.postService.getPostData(this.postId).subscribe(data =>{
        if(data==null){
          this.router.navigate(["/error"])
          return
        }
        // console.log(data)
        this.postData.setName(data.name)
        this.postData.setDescription(data.description)
        this.postData.setImgsrc(data.imgsrc)
        this.postData.setPrice(data.price)
        this.postData.setCategory(data.category)
        this.postData.setPostedBy(data.postedBy)
        this.postData.setPostId(data.postId)

        //fetch postedBy details
        this.authService.getUserDetails(data.postedBy+"").subscribe(data => {
          this.userDetails = data
        },error => this.router.navigate(["/error"]))
      },error => this.router.navigate(["/error"]))
    })
  }

  goBack(){
    history.back();
  }

  reportUser(postedBy){
    this.router.navigate(["/report"],{queryParams:{reportUser: postedBy}})
  }

  showUserDetails(){
    
  }

}
